import styled from "styled-components";
import { color } from "../../assets/colors";
import MainTitle from "../../components/MainTitle";
import SwiperTabMenu from "../../components/SwiperTabMenu";
import ArtistsOption from "./ArtistsOption";
import ArtistView from "./ArtistView";

import { useState } from "react";

const Artists = () => {
    const [filter, setFilter] = useState("");

    const onFilterTab = (
        e: React.MouseEvent<HTMLElement, MouseEvent>,
        filter: string,
    ) => {
        setFilter(filter);
    };

    return (
        <Block>
            <div className="border__line">
                <MainTitle
                    title="Artists"
                    subtitle="      ."
                />
            </div>
            <SwiperTabMenu onFilterTab={onFilterTab} />
            <ArtistsOption />
            <ArtistView filter={filter} />
        </Block>
    );
};

export default Artists;

const Block = styled.div`
    width: 100%;
    max-width: 1400px;
    min-height: 100%;
    margin: 70px auto;
    padding: 0 20px;
    box-sizing: border-box;
    & > .border__line {
        border-bottom: 1px solid ${color.gray03};
        padding-bottom: 45px;
        h2 {
            margin-bottom: 24px;
            font-family: "Ms-R";
        }
        span {
            color: ${color.black04};
        }
        @media screen and (max-width: 768px) {
            padding-bottom: 25px;
            h2 {
                margin-bottom: 12px;
            }
        }
    }
    @media screen and (max-width: 960px) {
        margin: 40px auto;
    }
`;
